<template>
    <div>
        <h1>PokeGuía</h1>
        <div>
            <label for="">Nombre:</label>
            <input type="text" v-model="pokemon.name">
            <input type="submit" value="Buscar" @click.prevent="fetchPokemon">
        </div>
        <div>
            <img :src="imagen" :alt="pokemon.name">
            <h2>Movimientos</h2>
            <div v-for="(movimiento, index) in movimientos" :key="index">
                <ul>
                    <li>
                        {{ movimiento.move.name }}
                    </li>
                </ul>
            </div>
        </div>
        <div>
            <h2>Habilidades</h2>
            <div v-for="(habilidad, index) in habilidades" :key="index">
                <ul>
                    <li>
                        {{ habilidad.ability.name}}
                    </li>
                </ul>
            </div>
        </div>

    </div>

</template>

<script>
export default {
    name: 'component-name',
    // props: {},
    data: function () {
        return {
            pokemon: {
                name: 'pikachu',
                imagen: '',
                movimientos: [],
                habilidades: [],
            },

        }
    },
    computed: {
        imagen() {
            return this.pokemon.imagen
        },
        movimientos() {
            return this.pokemon.movimientos
        },
        habilidades() {
            return this.pokemon.habilidades
        }
    },
    methods: {
        async fetchPokemon() {
            try {
                let data = await fetch(`https://pokeapi.co/api/v2/pokemon/${this.pokemon.name}`)
                if (!data.ok) throw ('Error en petición')

                let json = await data.json()


                this.addPokemon(json)

                console.log(data)

            }
            catch (error) {
                console.log(error)
            }
        },
        addPokemon(json) {
            this.pokemon.imagen = json.sprites.front_default
            this.pokemon.movimientos = json.moves
            this.pokemon.habilidades = json.abilities
        }

        // -- UI Modification
        // -- Data fetching
        // -- Data handling and transformations
        // -- Validations and Error handling
        // -- Other functions
        // -- Vuex mappings
    },
    // watch: {},
    // components: {},
    // mixins: [],
    // filters: {},
    // -- Lifecycle Methods
    created() {
        this.fetchPokemon()
    }
    // -- End Lifecycle Methods
}
</script>

<style scoped>
ul {
    display: flex;
    justify-content: center;
}
</style>